  #g-Ipad-box .g-artboard {
    margin:0 auto;
  }
  #g-Ipad-box p {
    margin:0;
  }
  .g-aiAbs {
    position:absolute;
  }
  .g-aiImg {
    display:block;
    width:100% !important;
  }
  .g-aiSymbol {
    position: absolute;
    box-sizing: border-box;
  }
  .g-aiPointText p { white-space: nowrap; }
  svg{ vector-effect: non-scaling-stroke; }
  #g-Panel1-Ipad {
    position:relative;
    overflow:hidden;
  }
  #g-Panel1-Ipad p {
    font-weight:500;
    font-size:15px;
    line-height:19px;
    filter:alpha(opacity=100);
    -ms-filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    opacity:1;
    letter-spacing:0em;
    text-align:left;
    color:rgb(0,0,0);
    text-transform:none;
    padding-bottom:0;
    padding-top:0;
    mix-blend-mode:normal;
    font-style:normal;
    height:auto;
    position:static;
  }
  #g-Panel1-Ipad .g-pstyle0 {
    color:rgb(255,255,255);
  }
  #g-Panel1-Ipad .g-pstyle1 {
    text-align:center;
  }
